import React, { Component } from 'react';
import s from './EpisodeList.module.css';
import { FaCheck } from 'react-icons/fa';

class EpisodeList extends Component {
  render() {
    const { episodes, toggleEpisode } = this.props;
    return (
      <div className={s.root}>
        {episodes && episodes.map(episode => (
          <div
            onClick={() => toggleEpisode(episode.anime_id, episode._id, episode.watched)}
            key={episode.episode}
            className={s.episode}
          >
            <div className={s.text}>
              <div className={s.episodeName}>Episode {episode.episode}</div>
              <div className={s.watchedAt}>
                {episode.watched && `Watched on ${episode.watchedAt.toLocaleDateString("en-US")}`}
              </div>
            </div>
            {episode.watched &&<FaCheck />}
          </div>
        ))}
      </div>
    );
  }
}

export default EpisodeList;