import React, { Component } from "react";
import { BSON, Stitch, RemoteMongoClient, UserPasswordCredential } from "mongodb-stitch-browser-sdk";
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import APP_ID from './constants/constants';
import Home from "./routes/Home/Home";
import Activity from "./routes/Activity/Activity";
import Anime from "./routes/Anime/Anime";
import AddAnime from "./routes/AddAnime/AddAnime";
import AddEpisodes from "./routes/AddEpisodes/AddEpisodes";
import Register from "./routes/Register/Register";
import Login from "./routes/Login/Login";
import Header from "./components/Header/Header";

const client = Stitch.hasAppClient(APP_ID)
  ? Stitch.getAppClient(APP_ID)
  : Stitch.initializeAppClient(APP_ID);

const mongoClient = client.getServiceClient(
  RemoteMongoClient.factory,
  "mongodb-atlas"
);

class App extends Component {
  constructor() {
    super();

    this.state = {
      animes: [],
      isAuthed: false,
      userId: null,
    };

    this.db = mongoClient.db("sugoi");

    this.register = this.register.bind(this);
    this.login = this.login.bind(this);
    this.logout = this.logout.bind(this);
    this.displayEpisodes = this.displayEpisodes.bind(this);
    this.displayAnimes = this.displayAnimes.bind(this);
    this.addAnime = this.addAnime.bind(this);
    this.deleteAnime = this.deleteAnime.bind(this);
  }

  componentDidMount = async () => {
    if (client.auth.hasRedirectResult()) {
      client.auth.handleRedirectResult().then(user => {
        this.setState({ isAuthed: client.auth.isLoggedIn })
      })
    }
  }

  addAnime = async anime => {
    // insert the anime into the remote Stitch DB
    // then re-query the DB and display the new animes
    const res = await this.db
      .collection('animes')
      .insertOne({
        owner_id: this.state.userId,
        name: anime.name,
        watched: 0,
        episode_total: parseInt(anime.episodes),
      });

    await this.addEpisodes(res.insertedId, anime.name, anime.episodes);

    const animes = await this.displayAnimes();
    this.setState({ animes });

    return res;
  }

  deleteAnime = async _id => {
    // delete animes and episodes with _id
    await this.db
      .collection('animes')
      .deleteOne({ _id });

    await this.db
      .collection('episodes')
      .deleteMany({ anime_id: _id });

    const animes = await this.displayAnimes();
    this.setState({ animes });
  }

  incrementWatched = async (_id, count) => {
    const res = await this.db
      .collection('animes')
      .updateOne( { _id }, { $inc: { watched: count } } )

    const animes = await this.displayAnimes();
    this.setState({ animes });

    return res;
  }

  displayAnimes = async (params = {}) => {
    // query the remote DB and update the component state
    const animes = await this.db
      .collection('animes')
      .find({ ...params, owner_id: this.state.userId }, { limit: 1000 })
      .asArray();

    return animes;
  }

  addEpisodes = async (anime_id, name, episode_total) => {
    // create array of episodes then insert them into collection episodes
    const episodes = [];

    for (let i = 1; i <= episode_total; i++) {
      const episode = {
        anime_id,
        name,
        owner_id: this.state.userId,
        episode: i,
        watched: false,
        watchedAt: new Date(),
      };

      episodes.push(episode);
    }

    await this.db
      .collection('episodes')
      .insertMany(episodes);

    const displayed = await this.displayEpisodes({ anime_id });

    return displayed
  }

  displayEpisodes = async (params = {}, projection = {}) => {
    const episodes = await this.db
      .collection('episodes')
      .find({ ...params, owner_id: this.state.userId }, { ...projection, limit: 1000 })
      .asArray();

    return episodes;
  }

  toggleEpisode = async (anime_id, _id, watched) => {
    const res = await this.db
      .collection('episodes')
      .updateOne({ _id }, { $set: { watched: !watched, watchedAt: new Date() } });

    await this.incrementWatched(anime_id, watched ? -1 : 1);

    return res;
  }

  watchEpisodes = async (anime_id, count, watchedAt = new Date()) => {
    const selection = await this.db
      .collection('episodes')
      .find({ anime_id, watched: false }, { limit: count })
      .asArray();
    
    const ids = selection.map(x => x._id);

    await this.db
      .collection('episodes')
      .updateMany({ _id: {$in: ids} }, { $set: { watched: true, watchedAt } });

    await this.incrementWatched(anime_id, count);
  }

  register = async ({ email, password } = {}) => {
    try {
      const res = await mongoClient.registerWithEmail(email, password);

      return res;
    } catch (e) {
        return e;
    }
  }

  login = async ({ email, password } = {}) => {
    const { isAuthed } = this.state

    if (isAuthed) {
      return;
    }

    const credential = new UserPasswordCredential(email, password)
    const res = await client.auth.loginWithCredential(credential)

    this.setState({
      isAuthed: true,
      userId: new BSON.ObjectId(res.id),
    })

    const animes = await this.displayAnimes();
    this.setState({ animes });
  }

  logout = async () => {
    client.auth.logout()
    this.setState({ isAuthed: false })
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  render() {
    return (
      <BrowserRouter>
        <Header isAuthed={this.state.isAuthed} logout={this.logout} />
        {this.state.isAuthed ? (
            <Switch>
              <Redirect from="/login" to="/" />
              <Redirect from="/register" to="/" />
              <Route
                default
                exact
                path="/"
                render={props => <Activity
                  {...props}
                  displayEpisodes={this.displayEpisodes}
                />}
              />
              <Route
                path="/list"
                render={props => <Home
                  {...props}
                  episodeStream={this.episodeStream}
                  animes={this.state.animes}
                />}
              />
              <Route
                path="/anime"
                render={props => <Anime
                  {...props}
                  toggleEpisode={this.toggleEpisode}
                  displayAnimes={this.displayAnimes}
                  displayEpisodes={this.displayEpisodes}
                  deleteAnime={this.deleteAnime}
                />}
              />
              <Route
                path="/addanime"
                render={props => <AddAnime
                  {...props}
                  addAnime={this.addAnime}
                />}
              />
              <Route
                path="/addepisodes"
                render={props => <AddEpisodes
                  {...props}
                  watchEpisodes={this.watchEpisodes}
                  displayAnimes={this.displayAnimes}
                />}
              />
            </Switch>
          ) : (
            <Switch>
              <Route
                default
                exact
                path="/login"
                render={props => <Login {...props} loginUser={this.login} />}
              />
              <Route
                path="/register"
                render={props => <Register {...props} registerUser={this.register} />}
              />
            </Switch>
        )}
      </BrowserRouter>
    );
  }
}

export default App;