import React, { Component } from 'react';
import s from './Header.module.css';
import { Link } from "react-router-dom";

const logout = logoutFunc => (
  <div className={s.buttons}>
    <Link to="/">My Feed</Link>
    <Link to="/List">My List</Link>
    <Link to="/AddAnime">Add Anime</Link>
    <Link to="/AddEpisodes">Add Episodes</Link>
    <div onClick={() => logoutFunc()} >Logout</div>
  </div>
);

const login = (
  <div className={s.buttons}>
    <Link to="/login">Login</Link>
    <Link to="/register">Register</Link>
  </div>
);

class Header extends Component {
  render() {
    return (
      <div className={s.root}>
        <Link to="/" className={s.logo}>Sugoi</Link>
        {this.props.isAuthed ? logout(this.props.logout) : login}
      </div>
    );
  }
}
export default Header;