import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import s from './AddAnime.module.css';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';

class AddAnime extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: '',
      episodes: '',
      home: false,
    };

    this.onClick = this.onClick.bind(this);
    this.changeEpisodes = this.changeEpisodes.bind(this);
  }

  onClick = async () => {
    const { name, episodes } = this.state;
    await this.props.addAnime({ name, episodes });

    this.setState({ home: true });
  }

  changeEpisodes = episodes => {
    if (!isNaN(episodes)) {
      this.setState({ episodes });
    }
  }

  render() {
    return (
      <div className={s.root}>
        {this.state.home && <Redirect to="/list" />}
        <div className={s.text}>
          Add an anime
        </div>
        <Input
          onChange={name => this.setState({ name })}
          placeholder="Name"
          type="text"
          value={this.state.name}
        />
        <Input
          onChange={this.changeEpisodes}
          placeholder="# Episodes Total"
          type="text"
          value={this.state.episodes}
        />
        <Button onClick={this.onClick}>Add anime</Button>
      </div>
    );
  }
}
export default AddAnime;