import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import s from './AddEpisodes.module.css';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';

class AddEpisodes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: '',
      date: '',
      episodes: '',
      home: false,
    };

    this.onClick = this.onClick.bind(this);
  }

  onClick = async () => {
    const { name, date, episodes } = this.state;
    
    const [ anime ] = await this.props.displayAnimes({ name });
    await this.props.watchEpisodes(anime._id, parseInt(episodes), new Date(date));

    this.setState({ home: true });
  }

  render() {
    return (
      <div className={s.root}>
        {this.state.home && <Redirect to="/" />}
        <div className={s.text}>
          Add episodes
        </div>
        <Input
          onChange={name => this.setState({ name })}
          placeholder="Anime"
          type="text"
          value={this.state.name}
        />
        <Input
          onChange={episodes => this.setState({ episodes })}
          placeholder="Episodes watched"
          value={this.state.episodes}
        />
        <Input
          onChange={date => this.setState ({ date })}
          placeholder="Date watched (MM/DD/YYYY)"
          type="text"
          value={this.state.date}
        />
        <Button onClick={this.onClick}>Add episodes</Button>
      </div>
    );
  }
}
export default AddEpisodes;