import React, { Component } from 'react';
import s from './AnimeList.module.css';
import { Link } from "react-router-dom";

class AnimeList extends Component {
  render() {
    const { animes } = this.props;
    return (
      <div className={s.root}>
        {animes && animes.map(anime => (
          <Link to={`/anime?id=${anime._id.toString()}`} key={anime._id.toString()} className={s.anime}>
            <div className={s.animeName}>{anime.name}</div>
            <div className={s.animeEpisodes}>{anime.watched} / {anime.episode_total}</div>
          </Link>
        ))}
      </div>
    );
  }
}

export default AnimeList;