import React, { Component } from 'react';
import { BSON } from "mongodb-stitch-browser-sdk";
import { Redirect } from "react-router-dom";
import s from './Anime.module.css';
import EpisodeList from '../../components/EpisodeList/EpisodeList';
import Button from '../../components/Button/Button';

class Anime extends Component {
  constructor(props) {
    super(props);

    this.state = {
      anime: {},
      episodes: [],
      home: false,
      deleting: false,
    };

    this.delete = this.delete.bind(this);
    this.toggleEpisode = this.toggleEpisode.bind(this);
  }

  componentDidMount = async () => {
    const id = new URLSearchParams(this.props.location.search).get('id');
    const objectId = new BSON.ObjectId(id);
    const [ anime ] = await this.props.displayAnimes({ _id: objectId });

    const episodes = await this.props.displayEpisodes({ anime_id: objectId });
    this.setState({ anime, episodes });
  }

  delete = async () => {
    this.setState({ deleting: true });

    await this.props.deleteAnime(this.state.anime._id);

    this.setState({ home: true, deleting: false })
  }

  toggleEpisode = async (anime_id, episodeId, watched) => {
    // TODO: watched on specific date
    const { _id } = this.state.anime;
    await this.props.toggleEpisode(anime_id, episodeId, watched);
    const episodes = await this.props.displayEpisodes({ anime_id: _id });
    const [ anime ] = await this.props.displayAnimes({ _id });
    this.setState({ anime, episodes });
  }

  render() {
    const { anime, episodes, home, deleting } = this.state;

    return (
      <div className={s.root}>
        {home && <Redirect to="/" />}
        <div className={s.header}>
          <div className={s.description}>
            <div className={s.name}>{anime.name}</div>
            <div className={s.watched}>
              {anime.watched} / {anime.episode_total} watched
            </div>
          </div>
          <div className={s.deleteColumn}>
            <Button onClick={this.delete}>Delete</Button>
            {deleting && <div className={s.deleting}>Deleting anime..</div>}
          </div>
        </div>
        <EpisodeList
          toggleEpisode={this.toggleEpisode}
          episodes={episodes}
        />
      </div>
    );
  }
}
export default Anime;