import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import s from './Register.module.css';
import Button from '../../components/Button/Button';
import Input from '../../components/Input/Input';

class Login extends Component {
  constructor(props) {
    super(props);

    this.state = {
      email: '',
      password: '',
      passwordVerification: '',
      error: null,
      toLogin: false,
    };

    this.register = this.register.bind(this);
  }

  register = async () => {
    const { registerUser } = this.props;
    const { email, password, passwordVerification } = this.state;

    if (password !== passwordVerification) {
      this.setState({ error: 'Passwords do not match' });

      return;
    }

    const res = await registerUser({ email, password })

    if (res && res.stack && res.message) {
      this.setState({ error: res.message});

      return;
    }

    console.log(res);

    this.setState({ toLogin: true });
  }

  render() {
    return (
      <div className={s.root}>
        {this.state.toLogin && <Redirect to="/login" />}
        <div className={s.text}>
          Create a new account
        </div>
        <Input
          onChange={email => this.setState({ email })}
          placeholder="Email"
          type="text"
          value={this.state.email}
        />
        <Input
          onChange={password => this.setState({ password })}
          placeholder="Password"
          type="password"
          value={this.state.password}
        />
        <Input
          onChange={passwordVerification => this.setState({ passwordVerification })}
          placeholder="Verify password"
          type="password"
          value={this.state.passwordVerification}
        />
        <Button onClick={this.register}>Sign in</Button>
        {this.state.error &&
          <div className={s.error}>{this.state.error}</div>
        }
      </div>
    );
  }
}
export default Login;