import React, { Component } from 'react';
import { Link } from "react-router-dom";
import CalendarHeatmap from 'react-calendar-heatmap';
import s from './Activity.module.css';

const groupBy = (array, f) => {
  let groups = {};
  array.forEach(function (o) {
    let group = JSON.stringify(f(o));
    groups[group] = groups[group] || [];
    groups[group].push(o);
  });
  return Object.keys(groups).map(function (group) {
    return groups[group];
  })
}

const calendarFormat = arr => {
  const values = [];

  let date = arr[0].watchedAt.toLocaleDateString('en-US');
  let count = 0;
  arr.forEach(x => {
    const currentDate = x.watchedAt.toLocaleDateString('en-US');
    if (date !== currentDate) {
      values.push({ date, count });
      date = currentDate;
      count = 1;
    } else {
      count++;
    }
  });

  values.push({ date, count });
  return values;
}

const selectByDate = (arr, date) => {
  const watched = [];
  arr.forEach(x => {
    if (x[0].watchedAt.toLocaleDateString('en-US') === date) {
      watched.push({ anime: x[0].name, count: x.length, date });
    }
  });

  return watched;
}

class Activity extends Component {
  constructor(props) {
    super(props);

    this.state = {
      feed: [],
      calendarLabels: [],
      calendarData: [],
      mouseOver: [],
      mousePosition: [0, 0],
      hovering: false,
      selectedDate: null,
    }
  }

  componentDidMount = async () => {
    const res = await this.props.displayEpisodes(
      { watched: true },
      { sort: { watchedAt: -1 } },
    );

    const feed = res.length && groupBy(res, function (item) {
      return [item.anime_id, item.watchedAt.toLocaleDateString('en-US')];
    });

    const calendarData = res.length && calendarFormat(res);
    this.setState({ feed, calendarData, calendarLabels: feed });
  }

  createFeed = async (date = {}) => {
    const res = await this.props.displayEpisodes(
      { ...date, watched: true },
      { sort: { watchedAt: -1 } },
    );

    return res.length && groupBy(res, function (item) {
      return [item.anime_id, item.watchedAt.toLocaleDateString('en-US')];
    });
  }

  squareClick = async value => {
    if (!value) {
      const feed = await this.createFeed();
      return this.setState({ feed });
    }

    const day = new Date(value.date);
    const nextDay = new Date(day.getFullYear(), day.getMonth(), day.getDate() + 1);

    const feed = await this.createFeed({
      watchedAt: {
        $gte: day,
        $lt: nextDay,
      }
    });

    this.setState({ feed });
  }

  render() {
    return (
      <div className={s.root}>
        {this.state.hovering && (
          <div
            style={{
              left: this.state.mousePosition[0],
              top: this.state.mousePosition[1],
            }}
            className={s.tooltip}
          >
            {this.state.mouseOver && this.state.mouseOver.map(row => (
              <div key={`${row.anime} - ${row.date}`} className={s.tooltipText}>
                {row.count} episodes of {row.anime}
                <span> on {row.date}</span>
              </div>
            ))}
          </div>
        )}
        <div className={s.header}>
          <div className={s.headerText}>Activity Feed</div>
        </div>
        <div onClick={(e) => { e.target === e.currentTarget && this.squareClick() }}>
          <div
            onClick={(e) => { e.target === e.currentTarget && this.squareClick() }}
            className={s.calendar}
          >
            <CalendarHeatmap
              startDate={new Date().setMonth(new Date().getMonth() - 6)}
              endDate={new Date()}
              values={(this.state.calendarData.length && this.state.calendarData) || [{ date: new Date('1/1/1990'), count: 1 }]}
              gutterSize={3}
              onMouseOver={(event, value) => (
                value && this.setState({
                  hovering: true,
                  mouseOver: selectByDate(this.state.calendarLabels, value.date),
                  mousePosition: [event.clientX, event.clientY]
                })
              )}
              onMouseLeave={() => this.setState({ hovering: false })}
              onClick={value => this.squareClick(value)}
              classForValue={value => {
                if (!value) {
                  return s['color-empty'];
                }

                if (value.count > 6) {
                  return s['color-scale-dark'];
                }

                return s[`color-scale-${value.count}`];
              }}
              transformDayElement={(element, value, index) => (
                React.cloneElement(element, { rx: '100%' })
              )}
            />
          </div>
        </div>
        <div className={s.feed}>
          {this.state.feed.length && this.state.feed.map(group => (
            <div key={group[0]._id} className={s.row}>
              <div className={s.text}>
                {`Watched ${group.length} episodes of `}
                <Link to={`/anime?id=${group[0].anime_id.toString()}`}>
                  {group[0].name}
                </Link>.
              </div>
              <div className={s.date}>
                {group[0].watchedAt.toLocaleDateString("en-US")}
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}
export default Activity;